.wisski {
  padding: 1em;
}

.wisski h3 {
  padding: 0;
}

.wisski a.pure-button {
  float: right;
  position: relative;
  bottom: 1em;
}

.wisski-broken-message {
  box-sizing: border-box;
  background-color: #ff7a7a;
  border: .5em solid red;
  padding: .5em;
}

.wisski {
  background-color: var(--wisski-status-color);
  border-left: 1em solid var(--wisski-accent-color);
  position: relative;
}

.wisski.broken:after {
  content: "STATUS FAILED";
  color: red;
  border: 1px solid var(--wisski-accent-color);
  background-color: #fff;
  padding: 0 1em;
  font-size: 1.5em;
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 0;
}

.wisski.running {
  --wisski-status-color: #9ada07;
  --wisski-accent-color: green;
}

.wisski.stopped {
  --wisski-status-color: #ff7a7a;
  --wisski-accent-color: red;
}

.disabled {
  color: gray;
}

.warning {
  background-color: #ffe4b5;
}

.overflow {
  overflow: auto;
}

.info-chip {
  text-align: center;
  border-radius: 5px;
  min-width: 75px;
  height: 15px;
  padding: 5px;
  display: block;
}

.info-chip.note {
  color: #fff;
  background-color: gray;
}

.info-chip.info {
  color: #fff;
  background-color: green;
}

.info-chip.warning {
  color: #000;
  background-color: #ff0;
}

.info-chip.error {
  color: #000;
  background-color: red;
}
